@import url("https://fonts.googleapis.com/css?family=Raleway:100,200,300,400,500,600,700,800,900&subset=latin-ext");
body {
  background: #E5E5E5;
  font-family: "Raleway", "Helvetica", "Arial", sans-serif; }

.content {
  padding-top: 15px;
  padding-bottom: 300px;
  background: white; }

.input-form {
  margin: 15px 0;
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15); }
  .input-form label {
    font-weight: bold;
    color: gray; }
  .input-form input {
    font-weight: normal;
    border-radius: 5px; }
  .input-form .button {
    margin: 15px auto;
    display: block;
    width: 100%;
    font-weight: bold;
    border-radius: 5px; }

footer {
  background: #E5E5E5;
  padding: 30px; }
  footer .copyright {
    font-size: 12px;
    color: #bbb; }

#navigation {
  background-color: white;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
  padding: 0; }
  #navigation .navigation-logo {
    margin-left: 15px; }
    #navigation .navigation-logo img {
      margin-right: 15px;
      margin-top: 15px;
      height: 32px;
      width: 32px;
      position: relative;
      top: -2px; }
    #navigation .navigation-logo span {
      font-weight: bold;
      position: relative;
      top: 8px; }
  #navigation .top-bar ul {
    background: white; }
    #navigation .top-bar ul li {
      background: white; }
  #navigation .top-bar-right {
    margin-right: 15px; }
    #navigation .top-bar-right ul {
      background-color: white; }
  #navigation .user-info {
    padding: 9px 0; }
    #navigation .user-info span span {
      padding: 10px;
      color: #5e5e5e;
      font-size: 14px; }
    #navigation .user-info .navigation-profile-image {
      height: 40px;
      width: 40px;
      border-radius: 15%; }
    #navigation .user-info .dropdown-pane {
      width: 195px;
      border-top: none;
      border-radius: 0 0 5px 5px;
      padding: 0;
      color: #5e5e5e; }
      #navigation .user-info .dropdown-pane a {
        color: #5e5e5e;
        font-size: 14px; }

.profile {
  padding: 40px;
  text-align: center; }
  .profile.profile-header img {
    border-radius: 200px;
    margin: 0 auto 55px auto; }
  .profile.profile-header p.name {
    font-size: 34px;
    font-weight: 700; }
  .profile.profile-content {
    padding-top: 0; }
    .profile.profile-content .tabs {
      border: none;
      border-bottom: solid 1px #ccc; }
    .profile.profile-content .tabs-title {
      display: inline-block;
      float: none; }
      .profile.profile-content .tabs-title a {
        color: #5e5e5e;
        font-size: 16px; }
      .profile.profile-content .tabs-title a[aria-selected='true'] {
        background: none;
        color: black;
        font-weight: 600;
        border-bottom: solid 2px #2199e8; }
    .profile.profile-content .tabs-panel {
      padding: 40px; }
    .profile.profile-content .tabs-content {
      border: none; }
  .profile .light {
    color: #5e5e5e; }

a.button, button.button {
  margin: 10px 0 10px 20px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 20px;
  font-weight: bold; }
  a.button.button-outlined, button.button.button-outlined {
    color: #2199e8;
    background-color: white;
    border: solid 1px #2199e8; }
    a.button.button-outlined:hover, button.button.button-outlined:hover {
      background-color: #2199e8;
      color: white; }
    a.button.button-outlined.button-danger, button.button.button-outlined.button-danger {
      color: #BB2124;
      border: solid 1px #BB2124; }
      a.button.button-outlined.button-danger:hover, button.button.button-outlined.button-danger:hover {
        background-color: #BB2124;
        color: white; }
    a.button.button-outlined.button-success, button.button.button-outlined.button-success {
      color: #22BB33;
      border: solid 1px #22BB33; }
      a.button.button-outlined.button-success:hover, button.button.button-outlined.button-success:hover {
        background-color: #22BB33;
        color: white; }
